import 'ScssComponents/cuts-info.scss';
import 'ScssComponents/product-pairs.scss';
import { Fancybox } from '@fancyapps/ui';
import '@fancyapps/ui/dist/fancybox.css';
import { exist } from '../utils';
import 'ScssComponents/universal/slider-controls.scss';

const cutsInfo = (
	sectionSelector = '.js-cuts-info',
	classLink = '.cuts_info__area',
	classLabel = '.cuts_info__label',
	content = '.js-cuts-info-content',
	classContent = '.cuts_info__content',
	btnNextSelector = '.js-cuts-info-next-button',
	btnPrevSelector = '.js-cuts-info-prev-button',
	controlSvg = '.js-control-svg',
) => {
	let activeTab = 0;
	let arrayTabs = {};
	const $sections = document.querySelectorAll(sectionSelector);
	if (!exist($sections)) return;

	$sections.forEach($section => {
		const $btnNext = $section.querySelector(btnNextSelector);
		const $btnPrev = $section.querySelector(btnPrevSelector);
		const $controlSvg = $section.querySelector(controlSvg);

		const $controlSvgArea = $controlSvg.getElementById('areas');
		const $controlSvgLabel = $controlSvg.getElementById('labels');

		if (!exist([$btnNext, $btnPrev, $controlSvg])) return;
		const $linkSelector = $controlSvgArea.querySelectorAll(classLink);
		const $svgLabels = $controlSvgLabel.querySelectorAll(classLabel);

		if (!$linkSelector.length) return;
		const activeLinkClass = `${classLink.slice(1)}--active_state`;
		const activeLabelClass = `${classLabel.slice(1)}--active_state`;
		const activeContentClass = `${classContent.slice(1)}--active_state`;

		$linkSelector.forEach((item) => {
			const $linkPath = item;
			const content = $section.querySelector(`[data-tab-content = '${$linkPath.dataset.tab}']`);
			const label = $section.querySelector(`[data-tab-label = '${$linkPath.dataset.tab}']`);
			if (!content) return;

			arrayTabs[item.dataset.tab] = {
				trigger: item,
				label,
				content,
			};
		});

		const addActiveState = () => {
			arrayTabs[activeTab]?.trigger?.classList.add(activeLinkClass);
			arrayTabs[activeTab]?.label?.classList.add(activeLabelClass);
			arrayTabs[activeTab]?.content?.classList.add(activeContentClass);
		};

		const removeActiveState = () => {
			arrayTabs[activeTab]?.trigger?.classList.remove(activeLinkClass);
			arrayTabs[activeTab]?.label?.classList.remove(activeLabelClass);
			arrayTabs[activeTab]?.content?.classList.remove(activeContentClass);
		};

		$btnNext.addEventListener('click', () => {
			removeActiveState();
			activeTab = activeTab >= Object.values(arrayTabs).length - 1 ? 0 : activeTab + 1;
			addActiveState();
		});

		$btnPrev.addEventListener('click', () => {
			removeActiveState();
			activeTab = activeTab === 0 ? Object.values(arrayTabs).length - 1 : activeTab - 1;
			addActiveState();
		});

		$controlSvg.addEventListener('click', (e) => {
			const { dataset } = e.target;
			if (dataset && dataset.tab) {
				removeActiveState();
				activeTab = Number(dataset.tab);
				addActiveState();
			}
		});
	});
};

export default cutsInfo;
